$hoverColor: #f55873;

.post-item-content {
    .post-item-title {
        color: #222;
        font-weight: 500;
        font-size: 15px;

        &:hover {
            color: $hoverColor;
        }
    }

    .post-item-des {
        margin-top: 8px;
        font-style: italic;
        font-weight: 500;
        color: #666;
    }

    .post-item-body {
        margin-top: 5px;
        color: #5c6066;
    }
}
.post-item-tag {
    margin-right: 10px;
    vertical-align: 2px;
    padding: 1px 4px;
    border-radius: 2px;
    font-size: 10px
}